import {defineStore} from 'pinia';
import {ref} from 'vue';
import io from 'socket.io-client';
//import { useToast } from "vue-toastification";

export const useVSUIStore = defineStore('vsui_store', () => {
  const socket = io({
    extraHeaders: {
      'type': 'web'
    }
  })
  const currentTab = ref('tasks')
  function toggle_current_tab() {
    if (currentTab.value == 'tasks') {
      console.log('toggling to settings')
      this.currentTab = 'settings'
    } else {
      console.log('toggling to tabs')
      this.currentTab = 'tasks'
    }
  }
  return { socket, currentTab, toggle_current_tab }
})